import React from 'react'
import {useEffect, useState} from "react";
import axios from "axios";
import {Layout, Spin, Menu} from "antd";
import 'antd/dist/reset.css';

const {Header, Content, Sider} = Layout;

const WeatherDisplay = ({city}) => {

    let [weatherData, setWeatherData] = useState(null);

    useEffect(() => {

        // const fetchData = async () => {
        //     //let url = `https://api.weather.yandex.ru/v2/informers?lat=${55.7522}&lon=${37.6156}&lang=ru_RU`;
        //     let url = `https://api.weather.yandex.ru/v2/forecast?lat=55.75396&lon=37.620393&extra=true`;
        //     let response = await fetch(url, {
        //         method: 'GET',
        //         headers: {
        //             'X-Yandex-API-Key': 'def61c74-6972-44dc-b216-9ddce22febb3',
        //         },
        //         //referrerPolicy: "strict-origin-when-cross-origin",
        //     });
        //
        //     if (response.ok) { //  HTTP-   200-299
        //         //    (.    )
        //         let json = await response.json();
        //         console.log(json);
        //     } else {
        //         alert(" HTTP: " + response.status);
        //     }
        // }
        // fetchData();


        const instance = axios.create({
            //     //withCredentials: true,
            //     //origin:'*',
            //     //baseURL: 'https://api.openweathermap.org/data/2.5/weather',
            baseURL: 'https://api.weather.yandex.ru/v2/forecast',
            headers: {
                'X-Yandex-API-Key': 'def61c74-6972-44dc-b216-9ddce22febb3',
            },
        });

        instance.get(`?lat=55.75396&lon=37.620393&extra=true`)
            //instance.get(`?lat=${lat}&lon=${lon}&appid=493dcd04898e06a3a498f0c4402b6c3e&lang=ru`)
            //instance.get(`?q=${city}&appid=493dcd04898e06a3a498f0c4402b6c3e&lang=ru`)
            .then(response => {
                if (response.status === 200) {
                    console.log(response.data);
                    setWeatherData(response.data);
                } else console.log('Error');
            });
    }, [city]);

    if (!weatherData) return <Spin tip="..." size="large"/>

    const iconUrl = "http://openweathermap.org/img/w/" + weatherData.weather[0].icon + ".png";
    return (
        <div>
            <h2>{weatherData.name} <img src={iconUrl} alt={'icon'}/></h2>
            <p>: <span style={{fontWeight: '500'}}>{Math.ceil(weatherData.main.temp - 273.15)}</span>,
                  <span
                    style={{fontWeight: '500'}}>{Math.ceil(weatherData.main.feels_like - 273.15)}</span></p>
            <p>: <span style={{fontWeight: '500'}}>{weatherData.weather[0].description}</span></p>
            <p> : <span style={{fontWeight: '500'}}>{weatherData.wind.speed} /</span></p>
        </div>
    );
}

const App = () => {
    const places = [
        {city: '', coordinates: [55.7522, 37.6156]},
        {city: '-', coordinates: [59.8944, 30.2642]},
        {city: 'New York', coordinates: [40.7143, -74.006]},
        {city: 'London', coordinates: [51.5085, -0.1257]},
    ];
    // const places = [
    //     {city: 'Moscow', name: 'Moscow'},
    //     {city: 'London', name: 'London'},
    //     {city: 'Paris', name: 'Paris'},
    //     {city: 'New York', name: 'Paris'},
    // ];

    let [activePlace, setActivePlace] = useState(0);

    return (
        <Layout className="site-layout-background">
            <Header theme="light">
                <h1 style={{color: "white"}}> React APP</h1>
            </Header>
            <Layout>
                <Sider style={{padding: "10px 5px"}}>
                    <h3 style={{color: "white", textAlign: 'center'}}> </h3>
                    <Menu
                        theme="dark"
                        mode="inline"
                        defaultSelectedKeys={['0']}
                    >
                        {places.map((place, id) => (
                            <Menu.Item key={id} onClick={() => {
                                setActivePlace(id)
                            }}>
                                {place.city}
                            </Menu.Item>
                        ))}
                    </Menu>
                </Sider>
                <Content style={{padding: "20px 50px"}}>
                    <WeatherDisplay coordinates={places[activePlace].coordinates}/>
                    {/*<WeatherDisplay city={places[activePlace].city}/>*/}
                </Content>
            </Layout>
        </Layout>
    )
};

export default App;
